.Entete {
    position: sticky;
    top: 0;
    height: 20px;
    margin: 0px;
    padding: 20px;
    color: #fff;
    background-color: #222;
    display: flex;
    align-items: center;
  
    ul {
      margin: 10px 0 0 0;
      padding: 0;
      list-style: none;
      display: flex;
      font-size: 0.8em;
  
      li {
        cursor: pointer;
      }
    
      li:not(:last-of-type) {
        padding-right: 15px;
        border-right: 1px solid white;
      }
    
      li:not(:first-of-type) {
        padding-left: 15px;
      }
  
      &.navPrincipale {
        margin-left: 50px;
        margin-right: 20px;
      }
  
      &.navUtilisateur {
        margin-left: auto;
      }
    }
  }